.testimonials {
  display: flex;
  gap: 1rem;
  padding: 0 2rem;
}
.left-container {
  display: flex;
  flex: 1 1;
  gap: 2rem;
  flex-direction: column;
  text-transform: uppercase;
  color: white;
}
.left-container > :nth-child(1) {
  color: var(--orange);
  font-weight: bold;
}

.left-container > :nth-child(2),
.left-container > :nth-child(3) {
  font-weight: bold;
  font-size: 3rem;
}
.left-container > :nth-child(4) {
  text-transform: none;
  text-align: justify;
  letter-spacing: 1px;
  line-height: 40px;
}
/* -----------------------Right Container-------------------- */
.right-container {
  flex: 1;
  position: relative;
}

.right-container > img {
  position: absolute;
  width: 17rem;
  height: 20rem;
  object-fit: cover;
  right: 8rem;
  top: 2rem;
}
.right-container > :nth-child(1) {
  position: absolute;
  width: 17rem;
  height: 20rem;
  border: 2px solid orange;
  background-color: transparent;
  right: 9rem;
  top: 0.9rem;
}
.right-container > :nth-child(2) {
  position: absolute;
  width: 17rem;
  height: 19rem;
  right: 7rem;
  top: 4rem;
  background: var(--planCard);
}
.arrows {
  display: flex;
  gap: 1rem;
  position: absolute;
  bottom: 1rem;
  left: 3rem;
}

.arrows > img {
  width: 1.5rem;
  cursor: pointer;
  margin: 0 0.75rem;
}

@media (max-width: 600px) {
  .arrows > img {
    margin: 0 1rem;
  }
}
/* --------------------media----------------------------- */
@media screen and (max-width: 768px) {
  .testimonials {
    flex-direction: column;
  }
  .left-container > :nth-child(2),
  .left-container > :nth-child(3) {
    font-size: xx-large;
  }
  .right-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  .right-container > div {
    position: relative;
    display: none;
  }
  .right-container > img {
    top: 0;
    right: 0;
    position: relative;
    align-self: center;
  }
  .right-container > :last-child {
    display: block;
    bottom: 0;
    left: 0;
    gap: 1rem;
  }
}
